.page-card__slider {
  grid-column: 6 span;
}
.page-card__content {
  grid-column: 6 span;
}
.page-card__box-title {
  display: flex;
  align-items: flex-start;
}
.page-card__box-title:not(:last-child) {
  margin-bottom: 30px;
}
.page-card__title {
  font-size: clamp(1.25rem, 0.943rem + 1.31vw, 2rem);
}
.page-card__favorite {
  width: 38px;
  height: 38px;
  background: url("../images/icons/favorite1.svg") left center/cover no-repeat;
}
.page-card__price {
  letter-spacing: 0.04em;
}
.page-card__price-name {
  display: inline-block;
  margin-right: 10px;
  font-size: 14px;
  text-transform: capitalize;
  color: #c5c5c5;
}
.page-card__price-descr {
  display: inline-block;
  font-size: 14px;
}
.page-card__price-price {
  display: inline-block;
  font-weight: 500;
  font-size: 28px;
  letter-spacing: -0.05em;
  color: #1d1d1d;
}
.page-card__peculiarities {
  margin: 24px 0;
  display: flex;
  align-items: center;
  gap: 9px;
  font-weight: 600;
  font-size: 12px;
  line-height: 100%;
  text-transform: uppercase;
  color: var(--white);
  text-transform: uppercase;
}
.page-card__peculiarities-sale {
  background-color: var(--red);
  padding: 6px 8px;
}
.page-card__peculiarities-new {
  background-color: var(--green);
  padding: 6px 8px;
}
.page-card__accordeon {
  margin-top: 20px;
}